@layer utilities {
  .scroll-smooth {
    scroll-behavior: smooth;
  }
  .focus-brand:focus {
    outline: none;
    box-shadow: 0 0 0 2px #fff, 0 0 0 4px #0D2340;
  }
  .glass {
    background: rgba(255, 255, 255, 0.7);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
  }
  .gradient-text {
    background: linear-gradient(135deg, #0D2340 0%, #C9A33A 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
  .shimmer {
    animation: shimmer 2s infinite;
    background: linear-gradient(to right, transparent 0%, rgba(255,255,255,0.3) 50%, transparent 100%);
    background-size: 1000px 100%;
  }
  .animate-pulse-dot {
    animation: pulse-dot 1.5s cubic-bezier(0.4, 0, 0.6, 1) infinite;
  }
  .animate-recording-pulse {
    animation: recording-pulse 2s infinite;
  }
  .animate-waveform {
    animation: waveform 0.6s ease-in-out infinite;
  }
}
@media print {
  header button,
  nav,
  .no-print {
    display: none !important;
  }
  body {
    background: white !important;
    color: black !important;
  }
  .max-w-7xl {
    max-width: 100% !important;
    padding: 20px !important;
  }
  .print\:block {
    display: block !important;
  }
  main > div:not(:first-child) {
    page-break-before: always;
    margin-top: 0 !important;
    padding-top: 20px !important;
  }
  .page-break-before {
    page-break-before: always;
  }
  .page-break-after {
    page-break-after: always;
  }
  .page-break-inside-avoid {
    page-break-inside: avoid;
  }
  * {
    -webkit-print-color-adjust: exact !important;
    print-color-adjust: exact !important;
    color-adjust: exact !important;
  }
  * {
    box-shadow: none !important;
    transition: none !important;
  }
  body, p, div, span {
    font-family: Arial, sans-serif !important;
  }
  main > div:nth-child(1)::before {
    content: "Overview";
    display: block;
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
    color: #0D2340;
    border-bottom: 2px solid #0D2340;
    padding-bottom: 10px;
  }
  main > div:nth-child(2)::before {
    content: "Detailed Analysis";
    display: block;
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
    color: #0D2340;
    border-bottom: 2px solid #0D2340;
    padding-bottom: 10px;
  }
  main > div:nth-child(3)::before {
    content: "Transcript";
    display: block;
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
    color: #0D2340;
    border-bottom: 2px solid #0D2340;
    padding-bottom: 10px;
  }
}
